body {
  font: 14px "LHF Uncial Caps", Futura, sans-serif;
  margin: 20px;
  background: url('static/background.jpg');

}

.card {
  border-radius: 15px;
  position: relative;
  transition: transform 0.8s,box-shadow .2s,border-color .2s;
  transform-style: preserve-3d;
  box-shadow: 0 0 3px rgba(0,0,0,0.15);

  width: 100%;
  height: 100%;

}

.side {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;

}
.card.hidden {

}

.card.justMatched,
.card.guessed {
  outline: 0.1em solid green;
}
.card.wrong {
  outline-color: red;
}

.card.visible {
  cursor: not-allowed;
  transform: rotateY(180deg);
}

.card > .symbol {
  margin: auto;
}

.score{

  font-size: 4em;
  margin: 0.2em;
  text-align: center;
  font-family:'LHF Uncial Caps', monospace;
  color: white;
}

.dif{
  font-size: 2em;
  margin: 0.2em;
  text-align: center;
  font-family:'LHF Uncial Caps', monospace;
  color: white;
}

.front{
}

.card .front {
  transform: rotateY(180deg);
  position: absolute;
  text-align: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: 1em;
}

.front::after {
   opacity: 0;
   transition: opacity 2s;
 }

.card-container {
  perspective: 600px;
  height: 167px;
  width: 112px;
  border-radius: 5px;
  padding: 10px;
  margin: 10px;
  cursor: grab;
}


.back{
  position: absolute;
  text-align: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;

}


.Timer {
  height: 10px;
}




.memory .cards-container {
  width: calc(90vw - (40px));
  height: calc(80vh - (40px));
  margin: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;

}


#root {
  width: 100vw;
  height: 95vh;
}


.memory {
  height: 90vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.navbar{
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: .5rem 1rem;

}

.restart{
  background: url("static/circle-cropped1.png");
  height: 51px;
  width: 51px;
}


.addlvl {
  color: #FFF;
  transition: all 0.5s;
  position: relative;
}
.addlvl::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(255,255,255,0.1);
  transition: all 0.3s;
}
.addlvl:hover::before {
  opacity: 0 ;
  transform: scale(0.5,0.5);
}
.addlvl::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
  border: 1px solid rgba(255,255,255,0.5);
  transform: scale(1.2,1.2);
}
.addlvl:hover::after {
  opacity: 1;
  transform: scale(1,1);
}
